div.ticker-container {
    padding: 5px 0;
    font-weight: bold;
    text-align: center;
    white-space: nowrap;
    animation-name: ticker;
    animation-duration: 5s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

@keyframes ticker {
    from { transform: translate(100vw) }
    to { transform: translate(-160vw) }
}
